.site-warp {
  .responsive-title-img {
    &::after {
      background-image: linear-gradient(-180deg, rgba(45, 58, 111, .5) 0, rgba(0, 0, 0, .5) 100%);
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
  }

  .post-full-title {
    font-size: 2.5rem;
  }

  .post-full-meta {
    a {
      color: var(--color-full-title);

      &:hover {
        text-decoration: var(--color-full-title) underline;
      }
    }
  }
  // 文章阅读量
  .post-full-meta-visitors {
    i {
      font-style: normal;
    }
  }
}

[data-theme="dark"] {
  .site-warp {
    .responsive-title-img {
      &::after {
        background-image: linear-gradient(-180deg, rgba(0, 0, 0, .5) 0, rgba(0, 0, 0, .8) 100%);
      }
    }

